@import url(https://fonts.googleapis.com/css2?family=Roboto+Condensed&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  text-align: center;
  font-family: 'Roboto Condensed', arial, sans-serif;
}

.main{
  min-height: 100vh;
  background-color: #3F8EFC;
  padding-bottom: 20px;
}


input{
    margin: 10px;
    padding: 5px 10px;
    font-size: 20px;
    background-color: #ADD7F6;
}

.form{
    font-size: 20px;
}

.form button{
    padding: 3px 10px;
    text-transform: uppercase;
    margin: 20px;
    font-size: 20px;
}

.addBtn{
    border-radius: 10rem;
    color: #fff;
    cursor: pointer;
    background-color: #2667FF;
    letter-spacing: .15rem;
    transition: 0.3s;
}

.addBtn:hover{
    background-color: #ADD7F6;
    color: #000;
}

.labelSpec{
    margin-right: 25px;
}



.mbtn{
    padding: 2px 5px;
    background-color: #deeaee;
    cursor: pointer;
    margin-left: 5px;
    border: 2px solid black;
    border-radius: 10rem;
    text-transform: uppercase;
    background-color: #2667FF;
    letter-spacing: .15rem;
    transition: 0.3s;
    color: #fff;
}

.mbtn:hover{
    background-color: #ADD7F6;
    color: #000;
}

.mbtnx{
    padding: 2px 5px;
    background-color: #deeaee;
    cursor: pointer;
    margin-left: 5px;
    border: 2px solid black;
    background-color: lightcoral;
    transition: 0.3s;
}

.mbtnx:hover{
    background-color: red;
}

.liMovie{
    margin-bottom: 10px;
}

.oneMovieA{
    width: 45%;
    margin: auto;
    padding: 10px 3px;
    border: 2px solid black;
    background-color: #87BFFF;
}

.oneMovieF{
    width: 30%;
    margin: auto;
    padding: 1px 1px;
    border: 2px solid black;
    background-color: #87BFFF;
}
